import { Overlay, Feature, Collection, MapBrowserEvent } from "ol";
import OverlayPositioning from "ol/OverlayPositioning";
import GeoJSON from "ol/format/GeoJSON.js";
import { Select } from "ol/interaction";
import { Geometry, LineString, Point } from "ol/geom";
import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import Value from "./Attributes.vue";
import { WFST } from "../../ol.hasura";

class OverlayAttibutes extends Overlay {
  vue: Value;
  constructor() {
    const value = new Value();
    value.$mount();

    super({
      element: value.$el as HTMLElement,
      positioning: OverlayPositioning.BOTTOM_CENTER,
      autoPan: true
    });

    this.vue = value;

    value.$on("about", data => {
      this.dispatchEvent({
        type: 'about',
        id: data,
      });
      this.cancel();
    });
  }

  show(
    feature: Feature<Point>,
    layer: VectorLayer,
    mapBrowserEvent: MapBrowserEvent
  ) {
    super.setPosition(mapBrowserEvent.coordinate);

    this.vue.$props.id = feature.getId();
    const val = feature.get('val') as { losses: number; category: number }[];
    this.vue.$props.cat1 = val.filter(el => el.losses === 1).length;
    this.vue.$props.cat2 = val.filter(el => el.losses === 2).length;
  }

  cancel() {
    super.setPosition(undefined);
  }
}

export { OverlayAttibutes };
