



















































































import { Component, Vue } from "vue-property-decorator";

import proj4 from "proj4";
import { register } from "ol/proj/proj4";
import { defaults as defaultInteractions } from "ol/interaction";
import Substrate from "@/lib/ol/Substrate";
import { Map } from "ol";
import EPGS32635 from "@/lib/ol/proj32635";
import View from "@/lib/ol/View";
import Bar from "ol-ext/control/Bar";

import { Accident } from "@/lib/ol/layers/Accident";
import CAccident from "@/lib/ol/control/Accident";

@Component
export default class App extends Vue {
  selectCategory = [1, 2, 3];
  optionsCategory = [
    {
      label: "автомобили",
      value: 1,
      color: "orange"
    },
    {
      label: "пешеходы",
      value: 2,
      color: "blue"
    },
    {
      label: "велосипеды",
      value: 3,
      color: "green"
    },
    {
      label: "только смертельные",
      value: 4,
      color: "red"
    }
  ];
  swithLossy = true;
  ranges = {
    "2020": [new Date(2020, 0, 1), new Date(2020, 11, 31)],
    "2019": [new Date(2019, 0, 1), new Date(2019, 11, 31)],
    "2018": [new Date(2018, 0, 1), new Date(2018, 11, 31)],
    "2017": [new Date(2017, 0, 1), new Date(2017, 11, 31)],
    "2016": [new Date(2016, 0, 1), new Date(2016, 11, 31)],
    "2015": [new Date(2015, 0, 1), new Date(2015, 11, 31)]
  };
  dateRange = {
    startDate: this.ranges["2020"][0],
    endDate: this.ranges["2020"][1]
  };

  localeData = {
    direction: "ltr",
    format: "dd.mm.yyyy",
    separator: " - ",
    applyLabel: "Принято",
    cancelLabel: "Закрыть",
    weekLabel: "W",
    customRangeLabel: "Custom Range",
    daysOfWeek: [6, ...Array(6).keys()]
      .map(el => new Date(2019, 6, el + 1))
      .map(el =>
        new Intl.DateTimeFormat("ru", { weekday: "short" }).format(el)
      ),
    monthNames: [...Array(12).keys()]
      .map(el => new Date(1970, el))
      .map(el => new Intl.DateTimeFormat("ru", { month: "long" }).format(el)),
    firstDay: 1
  };
  countFeatures = 0;
  lAccident: Accident;
  mounted() {
    proj4.defs(
      "EPSG:32635",
      "+proj=utm +zone=35 +ellps=WGS84 +datum=WGS84 +units=m +no_defs"
    );
    proj4.defs(
      "EPSG:3395",
      "+proj=merc +lon_0=0 +k=1 +x_0=0 +y_0=0 +datum=WGS84 +units=m +no_defs"
    );
    register(proj4);

    const view = new View({
      center: [539235.62979, 5969908.750949],
      zoom: 16,
      projection: EPGS32635,
      enableRotation: false,
      maxZoom: 20
    });

    const mainbar = new Bar({
      toggleOne: true,
      group: true,
      autoDeactivate: true
    });

    const map = new Map({
      target: "mapid",
      view,
      interactions: defaultInteractions({
        altShiftDragRotate: false,
        doubleClickZoom: false,
        shiftDragZoom: false
      }),
      layers: [Substrate(true)]
    });
    map.addControl(mainbar);
    const lAccident = new Accident(map, total => {
      this.countFeatures = total;
    });
    lAccident.connetctionMap();
    this.lAccident = lAccident;
    this.filtering();

    new CAccident(map, id => {
      this.$router.push({
        name: "editAccident",
        params: {
          id
        }
      });
    });

    // new CAccident(mainbar, lAccident.getSource() as WFST);
  }

  filtering() {
    const accident = this.lAccident;
    const layer = accident.layer;
    layer.setStyle(
      accident.getStyle({
        selectCategory: this.selectCategory,
        swithLossy: this.swithLossy,
        dates: this.dateRange
      })
    );
  }
}
