import LayerGroup from "ol/layer/Group";
import { Tile as TileLayer } from "ol/layer";
import { createXYZ } from "ol/tilegrid";
import { TileImage, BingMaps, XYZ, OSM } from "ol/source";

export default (visible: boolean) => {
  const osm = new TileLayer({
    source: new OSM(),
    visible: true
  });
  osm.set("baseLayer", true);
  osm.set("title", "OSM");

  const bing = new TileLayer({
    source: new BingMaps({
      key: "As1HiMj1PvLPlqc_gtM7AqZfBL8ZL3VrjaS3zIb22Uvb9WKhuJObROC-qUpa81U5",
      imagerySet: "Aerial",
      maxZoom: 19
    }),
    visible: false
  });
  bing.set("baseLayer", true);
  bing.set("title", "Bing");

  const yandex = new TileLayer({
    source: new XYZ({
      url: "http://vec0{1-4}.maps.yandex.net/tiles?l=map&x={x}&y={y}&z={z}",
      projection: "EPSG:3395",
      tileGrid: createXYZ({
        extent: [
          -20037508.342789244,
          -20037508.342789244,
          20037508.342789244,
          20037508.342789244
        ]
      })
    }),
    visible: false
  });
  yandex.set("baseLayer", true);
  yandex.set("title", "Yandex");

  const google = new TileLayer({
    source: new TileImage({
      url: "http://mt1.google.com/vt/lyrs=s&x={x}&y={y}&z={z}",
      projection: "EPSG:3857"
    }),
    visible: false
  });
  google.set("baseLayer", true);
  google.set("title", "Google");

  const substrate = new LayerGroup({
    visible,
    layers: [osm, bing, yandex, google]
  });
  substrate.set("title", "Подложка");

  return substrate;
};
