import VectorSource from "ol/source/Vector.js";
import { GeoJSON } from "ol/format.js";
import Event from "ol/events/Event.js";
import { all as allStrategy } from "ol/loadingstrategy.js";
import { Feature } from "ol";
import { Extent, getHeight } from "ol/extent";
import Projection from "ol/proj/Projection";

import gql from "graphql-tag";
import qraphql from "@/apollo-client";

import { get as getProjection } from "ol/proj";

import Query from "graphql-query-builder";
import { fromExtent } from "ol/geom/Polygon";
import { Options as OptionsVector } from "ol/source/Vector";
import Geometry from "ol/geom/Geometry";

enum DBKey {
  GEOM,
  IDENTITY
}

interface Options extends OptionsVector {
  operation: string[];
  fields: Array<{
    name: string;
    info?: DBKey[];
    transform?: {
      input?: (value: any) => any;
      output?: (value: any) => any;
    };
  }>;
  filter: { [key in string]: any } | null;
}

const vectorSourceEvent = {
  START_LOAD: "startLoad",
  END_LOAD: "endLoad",
  FAILED: "failed",
  START_TRANSACTION: "startTransaction",
  END_TRANSACTION: "endTransaction"
};

class VectorSourceEvent extends Event {
  code: number;
  constructor(type, code?: number) {
    super(type);

    if (code) {
      this.code = code;
    }
  }
}

// tslint:disable-next-line: max-classes-per-file
class WFST<GeomType extends Geometry = Geometry> extends VectorSource<
  GeomType
> {
  filter: Options["filter"] = {};
  addSet = new Set<Feature>();
  removeSet = new Set<Feature>();
  strategy_: Options["strategy"];
  graphql: Pick<Options, "operation" | "fields">;
  constructor(opt: Options) {
    super({
      format: new GeoJSON({
        geometryName: opt.fields.filter(({ info }) => {
          if (info) {
            return info.includes(DBKey.GEOM);
          }
          return false;
        })[0].name,
        // FIXME: нужно передавать эту хуйню
        dataProjection: getProjection("EPSG:4326"),
        featureProjection: getProjection("EPSG:32635")
      }),
      strategy: opt.strategy,
      features: opt.features
    });
    super.setLoader(this.loader);

    this.graphql = {
      operation: opt.operation,
      fields: opt.fields
    };
    this.filter = opt.filter;
  }

  public setStrategy(strategy: Options["strategy"]) {
    this.strategy_ = strategy;
  }

  public isEmptySave() {
    return this.addSet.size === 0 && this.removeSet.size === 0;
  }

  public async loader(
    extent: Extent,
    resolution: number,
    projection: Projection
  ) {
    this.dispatchEvent(new VectorSourceEvent(vectorSourceEvent.START_LOAD));

    try {
      const { fields, operation } = this.graphql;
      const names = operation.join("_");
      const geometryName = fields.filter(({ info }) => {
        if (info) {
          return info.includes(DBKey.GEOM);
        }
        return false;
      })[0].name;
      const q = new Query(names);
      const filter = this.filter;

      if (filter === null) {
        return;
      }

      if (extent.every(el => Number.isFinite(el))) {
        const polygonExtent = fromExtent(extent);
        const format = this.getFormat();
        const geom = JSON.parse(format.writeGeometry(polygonExtent));
        geom.crs = {
          type: "name",
          properties: {
            name: "EPSG:4326"
          }
        };
        if (filter) {
          q.filter({
            where: {
              _and: {
                ...filter,
                [geometryName]: {
                  _st_intersects: geom
                }
              }
            }
          });
        } else {
          q.filter({
            where: {
              [geometryName]: {
                _st_intersects: geom
              }
            }
          });
        }
      } else {
        q.filter({
          where: {
            ...filter
          }
        });
      }
      q.find(fields.map(({ name }) => name));

      const response = await qraphql.query<any>({
        query: gql`query {${q}}`
      });

      const result = response.data[names];

      const format = this.getFormat();
      result.forEach(item => {
        const feature = new Feature<GeomType>();

        fields.forEach(({ name, info, transform }) => {
          if (info && info.includes(DBKey.GEOM)) {
            const geom = item[name];
            const geomRead = format.readGeometry(geom) as GeomType;

            feature.setGeometryName(geometryName);
            feature.setGeometry(geomRead);
          } else if (info && info.includes(DBKey.IDENTITY)) {
            feature.setId(item[name]);
          } else {
            let value = item[name];
            if (transform && transform.input) {
              value = transform.input(value);
            }

            feature.set(name, value);
          }
        });

        this.addFeature(feature);
      });

      this.dispatchEvent(new VectorSourceEvent(vectorSourceEvent.END_LOAD));
    } catch (error) {
      console.error(error);
      this.dispatchEvent(new VectorSourceEvent(vectorSourceEvent.FAILED));
    }
  }

  public addFeature(feature: Feature<GeomType>, isSave = false) {
    if (!super.hasFeature(feature)) {
      super.addFeature(feature);
    }
    if (isSave) {
      this.addSet.add(feature);
    }
  }

  public removeFeature(feature: Feature<GeomType>, isSave = false) {
    super.removeFeature(feature);
    if (isSave) {
      this.addSet.delete(feature);
      this.removeSet.add(feature);
    }
  }

  public async save() {
    this.dispatchEvent(
      new VectorSourceEvent(vectorSourceEvent.START_TRANSACTION)
    );
    const insertSet = this.addSet;
    const removeSet = this.removeSet;

    try {
      const { fields, operation } = this.graphql;
      const names = operation.join("_");

      const format = this.getFormat();
      const insert = Array.from(insertSet);
      const features = insert.map(item => {
        const result = {};
        fields.forEach(({ name, info, transform }) => {
          if (info && info.includes(DBKey.GEOM)) {
            const geom = JSON.parse(format.writeGeometry(item.getGeometry()));
            geom.crs = {
              type: "name",
              properties: {
                name: "EPSG:4326"
              }
            };

            result[name] = geom;
          } else if (info && info.includes(DBKey.IDENTITY)) {
            result[name] = item.getId();
          } else {
            let value = item.get(name);
            if (transform && transform.output) {
              value = transform.output(value);
            }
            result[name] = value;
          }
        });

        return result;
      }) as any;

      const remove = Array.from(removeSet);
      const fRemove = remove
        .map(feature => feature.getId())
        .filter(id => id !== undefined);

      const response = await qraphql.mutate({
        mutation: gql`
          mutation ($features: [${names}_insert_input!]!, $id: [Int!]) {
            insert_${names}(on_conflict: {
              constraint: ${operation[1]}_pkey,
              update_columns: [${fields
                .filter(
                  ({ info }) =>
                    (info && !info.includes(DBKey.IDENTITY)) || !info
                )
                .map(({ name }) => name)
                .join(", ")}]
            },objects: $features) {
              returning {
                ${fields
                  .filter(({ info }) => info && info.includes(DBKey.IDENTITY))
                  .map(({ name }) => name)
                  .join("\n")}
              }
            }
            delete_${names}(where: {id: {_in: $id}}){
              returning {
                id
              }
            }
          }
        `,
        variables: {
          features,
          id: fRemove
        }
      });
      const { returning } = response.data[`insert_${names}`];

      (returning as any[]).forEach((item, index) => {
        const keys = Object.keys(item);
        keys.forEach(key => {
          fields.forEach(({ name, info, transform }) => {
            if (name === key) {
              if (info && info.includes(DBKey.IDENTITY)) {
                insert[index].setId(item[key]);
              }
              if (transform && transform.input) {
                insert[index].set(name, transform.input(item[key]));
              }
            }
          });
        });
      });

      this.dispatchEvent(
        new VectorSourceEvent(vectorSourceEvent.END_TRANSACTION)
      );
    } catch (error) {
      console.error(error);
      this.clear();
      this.dispatchEvent(new VectorSourceEvent(vectorSourceEvent.FAILED));
      throw new Error("не сохранено");
    } finally {
      insertSet.clear();
      removeSet.clear();
    }
  }
}

export { vectorSourceEvent, WFST, DBKey };
