import VectorLayer from "ol/layer/Vector";
import { WFST, DBKey } from "../ol.hasura";
import { all } from "ol/loadingstrategy";
import { Options } from "ol/layer/BaseVector";
import { Map, Observable } from "ol";
import { Style, Circle, Fill, Stroke } from "ol/style";

class Accident {
  layer: VectorLayer;
  constructor(private map: Map, cb: (total: number) => void) {
    const source = new WFST({
      operation: ["accident", "vcrash"],
      fields: [
        {
          name: "id",
          info: [DBKey.IDENTITY],
        },
        {
          name: "geom",
          info: [DBKey.GEOM],
        },
        {
          name: "val",
        },
        {
          name: "datetime",
        },
        {
          name: "category",
        },
        {
          name: 'view'
        }
      ],
      strategy: all,
      filter: {},
    });
    this.layer = new VectorLayer({ source });

    source.on("endLoad", () => {
      cb(source.getFeatures().length);
    });
  }

  connetctionMap() {
    this.map.addLayer(this.layer);
  }

  getStyle(params: any) {
    return (feature, resolution) => {
      const val = feature.get("val") as { losses: number; category: number }[];
      const date = new Date(feature.get("datetime").date);
      const category = feature.get('category');
      const view = feature.get('view');
      const radius = 10;
      const width = 2;

      if (params.dates.startDate <= date && params.dates.endDate >= date) {
        if ((val.every(el => el.losses === 3) || val.length === 0) && params.selectCategory.includes(4) === false) {
          return new Style({
            image: new Circle({
              radius,
              stroke: new Stroke({
                width,
                color: "black",
              }),
              fill: new Fill({
                color: "white",
              }),
            }),
          });
        } else if (val.some(el => [1, 2].includes(el.losses))) {
          let color = ``;
          if (category === 1 && params.selectCategory.includes(2)) {
            color = "blue";
          } else if (category === 3 && view === 6 && params.selectCategory.includes(3)) {
            color = "green";
          } else if (params.selectCategory.includes(1)) {
            color = "orange";
          }

          if (color !== "") {
            if (val.every(el => el.losses === 2)) {
              return new Style({
                image: new Circle({
                  radius,
                  stroke: new Stroke({
                    width,
                    color: "red",
                  }),
                  fill: new Fill({
                    color,
                  }),
                }),
              });
            } else {
              return new Style({
                image: new Circle({
                  radius,
                  fill: new Fill({
                    color,
                  }),
                }),
              });
            }
          }
        }
      }

      return new Style();
    };
  }
}

export { Accident };
