import Bar from "ol-ext/control/Bar";
import Toggle from "ol-ext/control/Toggle";

import { Select, Draw } from "ol/interaction";
import { createBox } from "ol/interaction/Draw";
import GeometryType from "ol/geom/GeometryType";
import Button from "ol-ext/control/Button";
import { Style, Stroke, Text, Fill, Circle } from "ol/style";
import { getCenter } from "ol/extent";
import { Point, Polygon } from "ol/geom";
import { shiftKeyOnly, never } from "ol/events/condition";
import Transform from "ol-ext/interaction/Transform";
import { WFST } from "../ol.hasura";
import { OverlayAttibutes } from "../plugin/Overlay/Attributes";
import { Map, Collection, Feature } from "ol";
const { CIRCLE } = GeometryType;

export default class {
  constructor(private map: Map, cd: (id) => void) {
    const overlay = new OverlayAttibutes();

    const iSAccident = new Select();

    overlay.on("about", (evt) => {
      cd(evt.id);
    });

    iSAccident.on("select", ({ selected, mapBrowserEvent }) => {
      const len = selected.length;
      if (len > 0) {
        const feature = selected[0];
        overlay.show(
          feature as Feature<Point>,
          iSAccident.getLayer(feature),
          mapBrowserEvent
        );
      } else {
        overlay.cancel();
      }
    });

    map.addOverlay(overlay);
    map.addInteraction(iSAccident);
  }
}
