import Vue from "vue";
import VueRouter from "vue-router";
import Analyz from '@/views/Analyz.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Analyz",
    component: Analyz
  },
  {
    path: "/accident",
    name: "newAccident",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "accident" */ "@/views/Accident.vue")
  },
  {
    path: "/accident/:id",
    name: "editAccident",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "accident" */ "@/views/Accident.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
